  padding-left: 14px;
  margin: -15px -15px 21px;
}

.box-drunk-formula .block {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  width: 100%;
  flex-direction: column;
