.box-drunk-formula .block input[type="number"],
.box-drunk-formula .block input[type="tel"] {
  width: 73px;
  height: 44px;
  line-height: 44px;
  text-align: center;
  outline: none;
  margin-bottom: 5px;
  margin-top: 0;
  color: #222;
  border: 1px solid #E6E6E6;
  border-radius: 2px;
  font-size: 16px;
