.wrapper_slide_thumb .swiper-slide:before {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  z-index: 1;
  opacity: 0;
}

.wrapper_slide_thumb .swiper-slide:after {
  width: 100%;
  height: 3px;
  background: #B52759;
  position: absolute;
  bottom: -5px;
  left: 0;
  content: '';
  z-index: 1;
  opacity: 0;
}

