  position: absolute;
  bottom: 35px;
  left: 0;
  background: #fff;
  height: 215px;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  transition-duration: 200ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
  border: 1px solid #E5E5E5;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 3px;
}

.box_comment_cms .paging .select-page .box-open-select .page-s {
  display: block;
  font-size: 15px;
  padding: 10px 15px;
}

.box_comment_cms .paging .select-page .box-open-select .page-s:hover {
  background: #F7F7F7;
}

.box_comment_cms .paging .select-page.active .box-open-select {
  bottom: 45px;
  opacity: 1;
  visibility: visible;
}

