.box_comment_cms .paging .button-page .btn-page {
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 36px;
  border: 1px solid #e5e5e5;
  border-radius: 3px;
  float: left;
  position: relative;
}

.box_comment_cms .paging .button-page .btn-page:before {
  width: 7px;
  height: 7px;
  border: solid #4F4F4F;
  border-width: 0 2px 2px 0;
  content: '';
  position: absolute;
  top: 14px;
  left: 11px;
  -webkit-transform: rotate(315deg);
  -ms-transform: rotate(315deg);
  transform: rotate(315deg);
}

.box_comment_cms .paging .button-page .btn-page .ic {
  fill: #4f4f4f;
  width: 10px !important;
  height: 10px !important;
  background: none !important;
  margin-top: 0;
}

.box_comment_cms .paging .button-page .btn-page:hover {
  border: 1px solid #757575;
  z-index: 1;
}

.box_comment_cms .paging .button-page .btn-page.prev-page:before {
  -webkit-transform: rotate(135deg);
  -ms-transform: rotate(135deg);
  transform: rotate(135deg);
  left: 15px;
}

.box_comment_cms .paging .button-page .btn-page.disable {
  background: #f7f7f7;
  border: 1px solid #f7f7f7;
}

.box_comment_cms .paging .button-page .btn-page.disable:before {
  border: solid #BDBDBD;
  border-width: 0 2px 2px 0;
}

.box_comment_cms .paging .button-page .btn-page.type_page {
  -moz-appearance: none;
  -webkit-appearance: none;
  padding: 0;
  margin: 0 5px;
  text-align: center;
  font-size: 15px;
  font-weight: 700;
  color: #222;
  min-width: 40px !important;
  max-width: 99.99% !important;
  transition: width 0.25s;
}

.box_comment_cms .paging .button-page .btn-page.type_page:focus {
  border: 1px solid #757575;
}

